
<style lang="less" scoped>
  .container {
    background-color: #eee;
  } // 头部
  .header {
    display: flex;
    height: 90px;
    background-color: #fff;
    div {
      flex: 2;
      padding-left: 30px;
      height: 90px;
    }
    span {
      flex: 3.333;
      display: inline-block;
      text-align: center;
      height: 90px;
      line-height: 90px;
      &:nth-child(1) {
        color: #999;
        text-align: left;
        text-indent: 1em;
      }
      &:nth-child(2) {
        font-size:30px;
      }
      &:nth-child(3) {
        text-align: right;
        padding-right: 1em;
        color: #44b336;
        box-sizing: border-box;
      }
    }
  } // 头部
  .area {
    padding: 30px 20px;
    &>div {
      padding-left:20px;
    }
    &>div {
      background-color: #fff;
      box-sizing: border-box;
      margin-bottom:20px;
      display: flex;
      span {
        font-size: 30px;
        display: inline-block;
        height: 90px;
        text-align: center;
        line-height: 90px;
        flex: 3;
      }
      input {
        border: none;
        outline: none;
        height: 90px;
        text-indent: 1em;
        flex: 7;
      }
    }
  }
  .submitBtn {
    width: 100%;
    text-align: center;
    &>button {
      width: 90%;
      height: 90px;
      background: #6dce67;
      text-align: center;
      line-height: 90px;
      color: #fff;
      font-size: 30px;
      border: none;
      outline: none;
      border-radius:20px;
    }
  }
</style>

<template>
  <div class="container">
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <div class="area">
      <div>
        <span>请输入新密码</span>
        <input placeholder="请输入新密码" type="password" v-model="password" autocomplete="off">
      </div>
      <div>
        <span>再次输入密码</span>
        <input placeholder="再次输入密码" type="password" v-model="repassword" autocomplete="off">
      </div>
    </div>
    <div class="submitBtn">
      <button @click="submitBtn">提交</button>
    </div>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        user: {},
        password: '',
        repassword: '',
        bool: true,
        centerTitle: '手机号找回密码',
      }
    },
    methods: {
      submitBtn() {
        if (this.password == this.repassword && sessionStorage.getItem('code')) {
          this.axios.post('/api/resetPasswdByPhone',{
            phone: this.$route.params.phone,
            password: this.password,
            code: sessionStorage.getItem('code')
          }).then((result) => {
            if(result.data.status === 'success'){
              this.$Toast({
                message: '修改成功',
                position: 'middle',
                duration: 1000
              })
              sessionStorage.removeItem('code')
              setTimeout(()=>{
                this.$router.push('/mine')
              },1000)
            }else{
              this.$Toast({
                message: result.data.content,
                position: 'middle',
                duration: 1000
              })
            }
          }).catch(err => {
            console.log(err)
          });
        } else {
          this.$Toast({
            message: '密码不一致或验证码失效',
            position: 'middle',
            duration: 1000
          })
        }
      },
    },
    mounted() {
    }
  }
</script>



